import React, {useState} from 'react'; 
import { makeStyles, Typography, useTheme } from '@material-ui/core';
import { Box} from '@material-ui/core';
import Paper from '@material-ui/core/Paper';

import style from './App.css';

const useStyles = makeStyles((theme) => ({
    card: {  
    position: "sticky",
    // position: "-webkit-sticky",
      padding: '1vh 1vh',
      backgroundColor: theme.palette.background.light,
      margin: '5vh 2.5vh 0vh 0vh',
      padding: '2vh'
    },
    
    skillSection: {
        display: "flex",
        flexDirection: "row",
        flexWrap: "wrap",
        justifyContent: "space-evenly",
        padding: "10px",
        // marginBottom: "5vh"
    },
    skillHeader: {
        textTransform: "capitalize",
        fontWeight: "medium",
        fontSize: "2vh",
        color: theme.palette.secondary.dark,
    },

    skill: {
        textTransform: "uppercase",
        fontWeight: "lighter",
        fontFamily: "Martel Sans",
        color: theme.palette.secondary.dark,
        margin: "5px 0 0 5px",
        transition: '0.5s, transform 2s',
    },
    neonText:{
        color: "#fff",
        textShadow: "0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff,"
                  + "0 0 42px #78B2BA, 0 0 82px #78B2BA, 0 0 92px #78B2BA,"
                  + "0 0 25px #78B2BA, 0 0 42px #78B2BA",
        transition: '0.5s, transform 2s',
    }
  }));
  
  
export function Skill(props){
    const theme = useTheme();
    const classes = useStyles(theme);
    
    return <Typography className={[classes.skill, props.glow ? classes.neonText : null]}> {props.text} </Typography>

}
export default function SkillCard(props) {
    const theme = useTheme();
    const classes = useStyles(theme);

    return (
        <Paper elevation={3} className={classes.card}>             
            {
                Object.entries(props.skills).map(([key, value]) => 
                {
                    return (
                        <Box>
                            {/* <Typography className={classes.skillHeader}>{key}</Typography> */}
                            <Box className={classes.skillSection}>
                                {
                                    value.map(el => 
                                        {
                                            return(<Skill text={el} glow={props.selectedSkills.includes(el)}/>);
                                        })
                                    }
                            </Box>
                        </Box>
                    )
                })
            }
        {/* 
            <Typography className={classes.skillHeader}>T echnologies</Typography>
            <Box className={classes.skillSection}>
                <Typography className={classes.skill}>Lorem</Typography>
                <Typography className={classes.skill}>Ipsum</Typography>
                <Typography className={classes.skill}>Lorem</Typography>
                <Typography className={classes.skill}>Ipsum</Typography>
                <Typography className={classes.skill}>Lorem</Typography>
                <Typography className={classes.skill}>Ipsum</Typography>
                <Typography className={classes.skill}>Lorem</Typography>
                <Typography className={classes.skill}>Ipsum</Typography>
                <Typography className={classes.skill}>Lorem</Typography>
                <Typography className={classes.skill}>Ipsum</Typography>
                <Typography className={classes.skill}>Lorem</Typography>
                <Typography className={classes.skill}>Ipsum</Typography>
                <Typography className={classes.skill}>Lorem</Typography>
                <Typography className={classes.skill}>Ipsum</Typography>

            </Box> */}
        </Paper>
    );
}  